body {
  font-family: Arial, sans-serif;
  margin: 0;
  padding: 0;
  background: #001e4d;
}

.App {
  max-width: 600px;
  margin: 40px auto;
  padding: 20px;
  background-color: #f0f0f0;
  border: 1px solid #ddd;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

.quiz-maker {
  /* margin-top: 40px; */
  padding: 20px;
  font-family: Arial, sans-serif;
}

.input-group {
  margin-bottom: 10px;
}

.input-group label {
  margin-right: 10px;
}

.input-group input {
  padding: 5px;
  width: 300px;
}

.buttons {
  margin-top: 20px;
}

.buttons button {
  margin: 5px;
  padding: 10px 15px;
}

.buttons,
.bottom-buttons {
  display: flex;
  flex-direction: column;
  gap: 10px;
}

button {
  padding: 10px 20px;
  margin: 5px 0;
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

button:hover {
  background-color: #0056b3;
}

.bottom-buttons {
  margin-top: 20px;
  align-items: center;
}

.error-message {
  color: red;
  margin-top: 10px;
}

.quiz-display {
  margin-top: 20px;
}

.quiz-header {
  text-align: center;
}

.quiz-header h1 {
  font-size: 2rem;
  margin-bottom: 0.5rem;
}

/* .quiz-header hr {
  width: 50%;
  border: none;
  border-top: 2px solid black;
  margin: auto;
  margin-bottom: 1rem;
} */

.quiz-question {
  margin-bottom: 15px;
}

.score-display {
  margin-top: 20px;
  font-weight: bold;
  font-size: 1.2em;
}